.body1{font-size:1rem;font-weight:400;line-height:1.75rem}.body2,.main_header .right_wrapper .contact_us,.main_header .right_wrapper nav{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn,.main_header .right_wrapper .contact_us{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow,.main_header .right_wrapper .yellow.contact_us{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover,.main_header .right_wrapper .yellow.contact_us:hover{background-color:#fde4a6}.btn.dark_blue,.main_header .right_wrapper .dark_blue.contact_us{color:#fff;background-color:#282938}.btn.dark_blue:hover,.main_header .right_wrapper .dark_blue.contact_us:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.main_header{display:flex;align-items:center;justify-content:space-between;height:4em;padding:1em 10rem;background-color:#1c1e53}.main_header .logo{font-size:1.5rem;color:#fff}.main_header .right_wrapper{display:flex;align-items:center;height:100%}.main_header .right_wrapper nav{display:flex;align-items:center}.main_header .right_wrapper nav ul{display:flex;list-style:none}.main_header .right_wrapper nav ul li{margin-left:2em;white-space:nowrap}.main_header .right_wrapper nav ul li a{text-decoration:none;color:#bbbbcb}.main_header .right_wrapper nav ul li a.active{color:#fff}.main_header .right_wrapper nav ul li a:not(.active):hover{text-decoration:underline}.main_header .right_wrapper .contact_us{height:100%;margin-left:3em;color:#fff;border:2px solid rgba(244,246,252,.2);background:rgba(0,0,0,0)}